export default {
	components: {
		tabbar,
		noData,
		uniLoadMore
	},
	data() {
		return {
			tabList:[{id:0,name:'全部订单'},{id:1,name:'有效订单'},{id:2,name:'待支付'}],
			tabIndex:0,
			hasData: false,
			noDataIsShow: false,
			loadingType: 0, //0加载前，1加载中，2没有更多了
			pageSize:6,
			page: 1,
			isLoad: false,
			datalist:[],//数据
		}
	},
	onShow() {
		
	},
	 computed: {
	    tabStyle(){
	      return ((750/this.tabList.length)*this.tabIndex)+(((750/this.tabList.length)-80)/2)
	    }
	  },
	methods: {
		goUrl(url){
		  uni.navigateTo({
			url:url
		  })
		},
		cliTab(index){
		  this.tabIndex = index
		},
	}
}
